import m from 'mithril'

import GenericTableComponent from 'components/generic_table'

import {cast} from '../../dice'
import common from '../common_styles.js'
import styles from './styles.js'
import classnames from 'classnames'
import * as rules from '../../rules.js'

function printableSign (num) {
  return num === 0 ? '±' : num < 0 ? '-' : '+'
}

class GeneralInfomationComponent {
  view (vnode) {
    const character = vnode.attrs.character
    const {
      name,
      age,
      gender,
      affiliation,
      socialStanding,
      profession,
      height,
      weight
    } = character.generalInformation

    return m(GenericTableComponent, {
      columns: [
        {title: 'Field'},
        {title: 'Value'},
      ],
      getRows: () => [
        ['Character name', name],
        ['Gender', gender],
        ['Social standing', socialStanding],
        ['Weight (kg)', weight],
        ['Height (cm)', height],
        ['Age', age],
        ['Affiliation', affiliation],
        ['Profession', profession]
      ],
      renderRow: ([prop, val]) => m('tr', [
        m('td', prop),
        m('td', {className: common.textAlignRight}, val),
      ])
    })
  }
}

class BasicCapabilitiesComponent {
  view (vnode) {
    return m(GenericTableComponent, {
      columns: [
        {title: 'Capability'},
        {title: 'Value'},
      ],
      getRows: () => {
        const {
          STR,
          PHY,
          COR,
          INT,
          MST,
          PER
        } = (vnode.attrs.character.basicCapabilities)
        return [
          ['STR', STR],
          ['PHY', PHY],
          ['COR', COR],
          ['INT', INT],
          ['MST', MST],
          ['PER', PER],
        ]
      },
      renderRow: ([prop, val]) => m('tr', [
        m('td', prop),
        m('td', {className: common.textAlignRight}, val),
      ])
    })
  }
}

class CombatStatisticsComponent {
  view (vnode) {
    const character = vnode.attrs.character
    const bc = character.basicCapabilities

    const ma = rules.movementAllowance(bc)
    const ob = rules.offensiveBonus(bc)
    const act = rules.actionsPerRound(bc)
    const db = rules.defensiveBonus(bc)
    const pb = rules.perceptionBonus(bc)
    const ib = rules.initiativeBonus(bc)
    const {totalBodyPoints} = rules.bodyPoints(bc)

    return m(GenericTableComponent, {
      columns: [
        {title: 'Property'},
        {title: 'Value'}
      ],
      getRows: () => [
        ['LEVEL', character.generalInformation.level],
        ['Total BP\'s', totalBodyPoints],
        ['Movement allowance, sqr/act', ma.squaresPerAction],
        ['Movement allowance, m/min', ma.metersPerMinute.toFixed(0)],
        ['Initiative bonus (IB)', printableSign(ib) + ib.toString()],
        ['Offensive bonus (OB)', printableSign(ob) + ob.toString()],
        ['Actions / CR', act],
        ['Defensive bonus', printableSign(db) + db.toString()],
        ['Perception bonus', printableSign(pb) + pb.toString()],
      ],
      renderRow: ([prop, val]) => m('tr', [
        m('td', prop),
        m('td', {className: common.textAlignRight}, val),
      ])
    })
  }
}

class BodyPointsComponent {
  view (vnode) {
    const character = vnode.attrs.character
    const bc = character.basicCapabilities
    const {bodyParts} = rules.bodyPoints(bc)
    return m(GenericTableComponent, {
      columns: [
        {title: 'Body part'},
        {title: 'Points'},
      ],
      getRows: () => Object.keys(bodyParts).map(bp => [
        bp,
        bodyParts[bp]
      ]),
      renderRow: ([prop, val]) => m('tr', [
        m('td', prop),
        m('td', {className: common.textAlignRight}, val),
      ])
    })
  }
}

class SkillTableComponent {
  view (vnode) {
    const skills = vnode.attrs.skills

    return m(GenericTableComponent, {
      getRows: () => skills.map(([name, value]) => [
        name,
        value
      ]),
      renderRow: ([prop, val]) => m('tr', [
        m('td', prop),
        m('td', {className: common.textAlignRight}, val),
      ])
    })
  }
}

class SkillsComponent {
  view (vnode) {
    const character = vnode.attrs.character

    return m('div', {className: common.flex}, [
      m('div', {className: common.spacingRight}, [
        m('h4', 'Combat'),
        m(SkillTableComponent, {skills: character.skills.combat})
      ]),
      m('div', {className: common.spacingRight}, [
        m('h4', 'Firearms'),
        m(SkillTableComponent, {skills: character.skills.firearms})
      ]),
      m('div', {className: common.spacingRight}, [
        m('h4', 'Communication'),
        m(SkillTableComponent, {skills: character.skills.communication})
      ]),
      m('div', {className: common.spacingRight}, [
        m('h4', 'Technical'),
        m(SkillTableComponent, {skills: character.skills.technical})
      ]),
      m('div', {className: common.spacingRight}, [
        m('h4', 'Movement'),
        m(SkillTableComponent, {skills: character.skills.movement})
      ]),
      m('div', {className: common.spacingRight}, [
        m('h4', 'Special'),
        m(SkillTableComponent, {skills: character.skills.special})
      ]),
    ])
  }
}

export default class CharacterComponent {
  view (vnode) {
    const character = vnode.attrs.character

    return m('div', {}, [
      m('h3', `Character sheet for: ${character.generalInformation.name}`),
      m('div', {}, [
        m('div', {className: styles.fwTables}, [
          m('div', {className: common.flex}, [
            m('div', {className: common.spacingRight}, [
              m('h3', 'General Information'),
              m(GeneralInfomationComponent, {character}),
            ]),
            m('div', {className: common.spacingRight}, [
              m('h3', 'Basic Capabilities'),
              m(BasicCapabilitiesComponent, {character}),
            ]),
            m('div', {className: common.spacingRight}, [
              m('h3', 'Combat Statistics'),
              m(CombatStatisticsComponent, {character}),
            ]),
            m('div', {className: common.spacingRight}, [
              m('h3', 'Body Points'),
              m(BodyPointsComponent, {character}),
            ]),
          ]),
        ]),
        m('div', {}, [
          m('h3', 'Skills'),
          m(SkillsComponent, {character})
        ]),
      ]),
    ])
  }
}
