import getCss from 'csjs/get-css'

import commonStyles from './components/common_styles.js'
import mainStyles from './components/main/styles.js'
import tabsStyles from './components/tabs/styles.js'
import characterStyles from './components/character/styles.js'

export function registerStyles (document) {
  const style = document.createElement('style')
  const css = [
    commonStyles,
    mainStyles,
    tabsStyles,
    characterStyles,
  ].reduce((result, next) => `${result}
${getCss(next)}`, '')
  style.innerText = css
  document.body.appendChild(style)
}
