import m from 'mithril'

export default class GenericTableComponent {
  constructor (vnode) {
    const attrs = vnode.attrs
    this.async = attrs.async
    this.columns = attrs.columns || []
    this.getRows = attrs.getRows
    this.renderRow = attrs.renderRow
  }

  view (vnode) {
    return m('table', [
      this.getHead(),
      m('tbody', [
        this.getRows().map(this.renderRow)
      ])
    ])
  }

  getHead () {
    if (this.columns.length === 0) {
      return void 0
    }

    return m('thead', [
      m('tr', this.columns.map(col => m('th', col.title)))
    ])
  }
}
