import m from 'mithril'
import styles from './styles.js'

import TabsComponent from '../tabs'
import CharacterComponent from '../character'
import RuleTables from '../rule_tables'

import protoDefs from '../../random/prototype_definitions.js'
import {getCharacter} from '../../random'
import {cast} from '../../dice.js'

export default class MainComponent {
  view () {
    const tabs = m(TabsComponent, {
      navigation: [
        {
          title: 'Characters',
          content: () => m(CharacterComponent, {character: getCharacter(protoDefs.criminals.streetScum, cast(1, 10))})
        },
        {title: 'Tables', content: () => m(RuleTables)},
        {title: 'Combat', content: () => m('div', 'Combat')},
        {title: 'Import/Export', content: () => m('div', [
          m('div', 'Import'),
          m('div', 'Export'),
        ])},
      ]
    })
    return m('div', {className: styles.main}, [
      tabs
    ])
  }
}
