import m from 'mithril'
import classnames from 'classnames'
import styles from './styles.js'

export default class TabsComponent {
  static DEFAULT_NAVIGATION () {
    return [{title: 'EMPTY', content: () => m('div', 'EMPTY')}]
  }

  constructor (vnode) {
    this.navigation = vnode.attrs.navigation || TabsComponent.DEFAULT_NAVIGATION()
    this.selectedNode = this.navigation[0]
  }

  view (vnode) {
    return m('div', {className: styles.component}, [
      m('div', {className: styles.tabNodeList}, this.getTabs()),
      m('div', {className: styles.tabContent}, this.getCurrentContent())
    ])
  }

  getTabs () {
    return this.navigation.map(node => m('button', {
      type: 'button',
      className: classnames(styles.tabNode.toString(), {
        [styles.active.toString()]: node == this.getCurrentNode()
      }),
      onclick: this.onTabClick.bind(this, node)
    }, node.title))
  }

  onTabClick (node) {
    this.selectedNode = node
  }

  getCurrentNode () {
    return this.navigation.filter(node => node === this.selectedNode)[0]
  }

  getCurrentContent () {
    return this.getCurrentNode().content()
  }
}
