import {pickOne} from '../utilities'
import * as bauhaus from './bauhaus.js'
import * as capitol from './capitol.js'
import * as imperial from './imperial.js'
import * as mishima from './mishima.js'

const cybertronic = [
  bauhaus,
  capitol,
  imperial,
  mishima
].reduce((out, next) => {
  out.rawMale += '\n' + next.rawMale
  out.rawFemale += '\n' + next.rawFemale
  return out
}, {
  rawMale: '',
  rawFemale: '',
})

export function getRandomName (corp, gender) {
  const dispatch = {bauhaus, capitol, imperial, mishima, cybertronic}
  const obj = dispatch[corp]
  let src = null
  if (gender === 'M') {
    src = obj['rawMale']
  } else {
    src = obj['rawFemale']
  }
  src = src.split('\n')
  return pickOne(src)
}
